import React, { Component, Fragment } from 'react';
import Service from '../services';
import { Context } from '../provider/index';
import axios from 'axios';

import Header from '../header/Header';
import SideNav from './SideNav';
import PostLayout from './PostLayout';
import ProfileLayout from '../profile/Profile';
import QuestionLayout from './QuestionLayout';
import UsersPanel from './UsersPanel';

class Main extends Component {
    state = {
        feed: [],
        public: [],
        postForm: false,
        activity: null
    }

    static contextType = Context

    getUserLocation =  () => {
        if (navigator.geolocation) {
            this.service = new Service();
            navigator.geolocation.getCurrentPosition( (position) => {

                axios.get(`https://api.opencagedata.com/geocode/v1/json?q=${position.coords.latitude}+${position.coords.longitude}&key=614cbe4035a04205bc7d3451acfe5004`)
                    .then(res => {
                        const components = res.data.results[0].components

                        const county = components.county
                        const state = components.state_code
                        console.log(res)
                        // this.setState({
                        //     location: {
                        //         county,
                        //         state
                        //     },
                        //     loading: false
                        // })
                    })
                    .catch(err => {
                        console.log(err)
                    })
            }, (error) => {
                if (error.code == error.PERMISSION_DENIED) {
                    // this.setState({
                    //     location: null,
                    //     loading: false
                    // })

                    console.log("You denied Geolocation")
                }
            })  

        } else {
            console.log('Geolocation is not supported');
        }
        
    }

    componentDidMount() {
        this.getInfo()
        // this.getUserLocation()
        // window.dropdownTrigger()
    }

    getInfo = async () => {
        this.service = new Service()
        const res1 = await this.service.getFeedPosts();
        const res2 = await this.service.getPublicPosts();
        console.log('context user', this.context)
        if(this.context.user) {
            const res3 = await this.service.getUserActivity(this.context.user._id)
            const feed = res1.data.posts.filter( post => {
                return this.context.friendsIDs.includes(post.owner._id) || this.context.user._id === post.owner._id
            })
            this.setState({
                feed,
                public: res2.data.posts,
                activity: res3.data.activity
            })
        } else {
            this.setState({
                // feed,
                public: res2.data.posts
            })
        }
        // const feed = res1.data.posts.filter( post => {
        //     return this.context.friendsIDs.includes(post.owner._id) || this.context.user._id === post.owner._id
        // })
    }

    getFeedPosts = async () => {
        this.service = new Service()
        const res = await this.service.getFeedPosts();
        const feed = res.data.posts.filter( post => {
            return this.context.friendsIDs.includes(post.owner._id) || this.context.user._id === post.owner._id
        })
        this.setState({
            feed,
            postForm: false
        })
    }

    getPublicPosts = async () => {
        this.service = new Service()
        const res = await this.service.getPublicPosts();
        this.setState({
            public: res.data.posts,
            postForm: false
        })
    }
    
    renderHomepage = () => {
        if(this.props.match.path === "/feed") {
            return <PostLayout posts={this.state.feed} postForm={this.state.postForm} togglePostForm={this.togglePostForm} setPostForm={this.setPostForm} handlePostSubmit={this.handlePostSubmit} updatePost={this.updatePost} deletePost={this.deletePost} />
        } else if(this.props.match.path === "/public") {
            return <PostLayout posts={this.state.public} postForm={this.state.postForm} togglePostForm={this.togglePostForm} setPostForm={this.setPostForm} handlePostSubmit={this.handlePostSubmit} updatePost={this.updatePost} deletePost={this.deletePost} />
        } else if(this.props.match.path === "/profile") {
            return <ProfileLayout activity={this.state.activity} />
        } else if(this.props.match.path === "/question/:id") {
            return <QuestionLayout />
        }
    }

    togglePostForm = () => {
        this.setState({
            postForm: !this.state.postForm
        })
    }

    handlePostSubmit = (ev) => {
        ev.preventDefault();
        this.service = new Service()

        const form = new FormData(ev.target);
        const title = form.get('title');
        const description = form.get('description');

        this.service.createPost(title, description)
            .then( res => {
                if(this.props.match.path === "/feed") {
                    this.postForm.reset()
                    this.getFeedPosts()
                } else if(this.props.match.path === "/public") {
                    this.postForm.reset()
                    this.getPublicPosts()
                }
            }) .catch(err => {
                console.log(err)
            })
    }

    setPostForm = (el) => {
        this.postForm = el;
    }

    updatePost = (postInfo, cb) => {
        this.service = new Service();
        this.service.updatePost(postInfo)
            .then( res => {
                if(this.props.match.path === "/feed") {
                    cb()
                }

                if(this.props.match.path === "/public") {
                    cb()
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    deletePost = (id) => {
        this.service = new Service()
        this.service.deletePost(id)
            .then( res => {
                if(this.props.match.path === "/feed") {
                    this.getFeedPosts()
                }

                if(this.props.match.path === "/public") {
                    this.getPublicPosts()
                }
            } )
    }

    isActiveNav = (match, location) => {
        if(location.pathname === "/feed") return true
        return false
    }
    
    render() {
        return (
            <Fragment>
                <Header passSearchData={this.passSearchData} />
                <div className="my-container">
                    <div className="row">
                        <SideNav isActiveNav={this.isActiveNav} />


                        {this.renderHomepage()}

                        <UsersPanel />
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default Main;
