import React, {Component} from 'react';
import { Context } from '../provider/index';
import Question from './Question';
import Answer from './Answer';
import M from 'materialize-css';
import Services from '../services';

class ProfileLayout extends Component {
    static contextType = Context;

    state = {
        name: this.context.user.name,
        username: this.context.user.username,
        email: this.context.user.email
    }

    componentDidMount() {
        M.Tabs.init(document.querySelectorAll('.tabs'));
        M.Modal.init(document.querySelectorAll('.modal'));
    }

    handleInputChange = (ev) => {
        this.setState({
            [ev.target.name]: ev.target.value
        })
    }

    resetForm = () => {
        this.setState({
            name: this.context.user.name,
            username: this.context.user.username,
            email: this.context.user.email
        })
    }

    handleSubmitForm = (ev) => {
        ev.preventDefault()
        this.services = new Services();
        this.services.updateCurrentUser(this.state.name, this.state.username, this.state.email)
            .then( res => {
                this.context.getCurrentUser()
            })
            .catch(err => {
                console.log(err)
            })
    }

    render() {
        return(
            <div className="col s7 top-space">
                <div className="row">
                    <div className="col s4 center-align">
                        <img src={this.context.user.img} width="120px" />
                    </div>

                    <div className="col s6">
                        <h3 className="no-margin">{this.context.user.name}</h3>
                        <p><b>Username: </b>{this.context.user.username}</p>
                        <p><i class="material-icons">location_on</i>Miami</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col s9">
                        <ul className="tabs">
                            <li className="tab col s4"><a className="active no-padding" href="#questions">Questions</a></li>
                            <li className="tab col s4"><a className="no-padding" href="#answers">Answers</a></li>
                            <li className="tab col s4"><a className="no-padding" href="#friends">Friends</a></li>
                        </ul>
                    </div>
                    <div className="col s3 right-align">
                        <a className="btn modal-trigger" href="#edit-modal">Edit Profile</a>
                    </div>
                    <div id="questions" className="col s12">
                        <div className="row">
                            <div className="col s6">
                                <h4>Your Questions</h4>
                                {
                                    this.props.activity && this.props.activity.posts.map( post => <Question question={post} />)
                                }
                            </div>
                        </div>
                    </div>
                    <div id="answers" className="col s12">
                        <h4>Your Answers</h4>
                        {
                            this.props.activity && this.props.activity.answers.map( answer => <Answer answer={answer} />)
                        }
                    </div>           
                    <div id="friends" className="col s12">
                        <ul class="row collection users-collection friends-ul">
                            {
                                this.context.user.friends.friendsList.length > 0 ? (
                                this.context.user.friends.friendsList.map(friend => {
                                    return (
                                        <li className="col s5 collection-item avatar friends-card">
                                            <img src={friend.img} alt="" className="circle friends-img" />
                                            <span className="title">{friend.username}</span>
                                        </li>
                                    )
                                }))
                                :
                                <span>No Friends</span>
                            }
                        </ul> 
                    </div>                
                </div>
                <div id="edit-modal" className="modal">
                    <div className="modal-content">
                        <div className="row">
                            <div className="col s12">
                                <h5>Edit your profile</h5>
                            </div>
                        </div>
                        <div className="row">
                            <form id="edit-form" className="col s12" onSubmit={this.handleSubmitForm}>
                                <div className="row no-margin">
                                    <div className="input-field col s12">
                                        <input id="name" type="text" name="name" className="validate" value={this.state.name} onChange={this.handleInputChange} />
                                        <label id="name-label" for="name" className="active">Name</label>
                                    </div>
                                </div>

                                <div className="row no-margin">
                                    <div className="input-field col s12">
                                        <input id="username" type="text" name="username" className="validate" value={this.state.username} onChange={this.handleInputChange} />
                                        <label id="username-label" for="username" className="active">Username</label>
                                    </div>
                                </div>

                                <div className="row no-margin">
                                    <div className="input-field col s12">
                                        <input id="email" type="email" name="email" className="validate" value={this.state.email} onChange={this.handleInputChange} />
                                        <label id="email-label" for="email" className="active">Email</label>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button className="modal-close btn-flat" onClick={this.resetForm}>Cancel</button>
                        <button form="edit-form" className="modal-close btn">Submit</button>
                    </div>
                </div>
            </div> 
        )
    }
}

export default ProfileLayout;
