import React, {Fragment} from 'react';
import { Link } from 'react-router-dom';

function Dropdown(props) {
    return(
        <ul id={props.type} class='dropdown-content'>
            {
                props.userIcon &&
                <Fragment>
                    <li><Link to="/profile">Profile</Link></li>
                    <li><a onClick={props.userLogout}>Logout</a></li>
                </Fragment>
            }
            {
                props.notificationIcon &&
                <Fragment>
                    {
                        props.notifications.map(notification => {
                            return (
                                <li className="row no-margin">
                                    <div className="row no-margin">
                                        <span>{notification.name} wants to be your friend</span>
                                    </div>
                                    <div className="row no-margin">
                                        <div className="col s5">
                                            <button onClick={() => props.acceptRequest(notification._id)} className="btn-flat btn-small notif-accept-btn"><i className="notif-icon material-icons">check</i></button>
                                        </div>
                                        <div className="col s5 push-s2">
                                            <button onClick={() => props.deniedRequest(notification._id)} className="btn-flat btn-small notif-denied-btn"><i className="notif-icon material-icons">close</i></button>
                                        </div>
                                    </div>
                                </li>
                            )
                        })                        
                    }
                </Fragment>
            }
            {
                props.messageIcon &&
                <Fragment>
                    <li><a href="#!">Message 1</a></li>
                    <li><a href="#!">Message 2</a></li>
                    <li><a href="#!">Message 3</a></li>
                </Fragment>
            }
        </ul>
    )
}

export default Dropdown;
