import React, {Component, Fragment} from 'react';
import {Link} from 'react-router-dom';
import {Context} from '../provider/index';
import Services from '../services';

import Header from '../header/Header';
import SideNav from './SideNav';
import UsersPanel from './UsersPanel';
import UserResults from './UserResults';

class SearchLayout extends Component {
    state = {
        users: null,
        questions: null
    }

    static contextType = Context

    searchResults = (data) => {
        this.services = new Services()
        this.services.searchResults(data)
            .then( res => {
                this.setState({
                    users: res.data.users,
                    questions: res.data.questions
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    renderUserResults = () => {
        if(this.state.users) {
            return this.state.users.map( (user) => {
                if(this.context.user) {
                    if(user._id !== this.context.user._id) {
                        return (
                            <UserResults key={user._id} user={user} />
                        )
                    }
                } else {
                    return (
                        <UserResults key={user._id} user={user} />
                    )
                }
            })
        }
    }

    renderQuestionsResults = () => {
        if(this.state.questions) {
            return this.state.questions.map( question => {
                return (
                    <Link to={`/question/${question._id}`}>
                        <div class="collection col s11 push-s1 question-result-card">
                            <span class="title search-question-title">{question.title}</span>
                            <p className="question-content">{question.content}</p>
                            {/* <a href="#!" class="secondary-content">Follow</a> */}
                        </div>
                    </Link>
                )
            })
        }
    }

    componentDidMount() {
        const query = this.props.location.search.split('?q=')[1]
        console.log(query)
        this.searchResults(query)
    }

    shouldComponentUpdate(nextProps) {
        if(this.props.location.search !== nextProps.location.search) {
            console.log(nextProps.location.search.split('?q=')[1])
            this.searchResults(nextProps.location.search.split('?q=')[1])
            return true
        }

        return false
    }

    render() {
        return (
            <Fragment>
                <Header />
                <div className="my-container">
                    <div className="row">
                        <SideNav />
                        <div className="col s7">
                            <div className="row">
                                <div className="row no-margin">
                                    <h4 className="results-title">Results for Users:</h4>
                                </div>
                
                                <div className="row no-margin">
                                    {this.renderUserResults()}
                                </div>
                            </div>
                
                            <div className="row">
                                <div className="row no-margin">
                                    <h4 className="results-title">Results for Questions:</h4>
                                </div>
                
                                <div className="row no-margin">
                                    {this.renderQuestionsResults()}
                                </div>
                            </div>
                        </div>
                        {
                            this.context.user &&
                            <UsersPanel />
                        }
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default SearchLayout