import React, {Fragment} from 'react';

function Modal(props) {
    return(
        <div id={props.type} className="modal">
            <div className="modal-content">
                <div className="row">
                    <div className="col s12 center-align">
                        <h4 className="modal-title">{props.title}</h4> 
                    </div>
                </div>

                <div className="container">
                    <div class="row">
                        <form ref={props.setForm} id={props.type} class="col s12" onSubmit={props.handleSubmit}>
                            {
                                props.signup ?
                                <Fragment>
                                    <div class="row auth-form-row">
                                        <div class="input-field col s12">
                                            <i class="material-icons prefix auth-form-icon">account_circle</i>
                                            <input id="name" type="text" name="name" class="validate" />
                                            <label htmlFor="name">Name</label>
                                        </div>
                                    </div>
                                    <div class="row auth-form-row">
                                        <div class="input-field col s12">
                                            <i class="material-icons prefix auth-form-icon">account_circle</i>
                                            <input id="username" type="text" name="username" class="validate" />
                                            <label htmlFor="username">Username</label>
                                        </div>
                                    </div>

                                    <div class="row auth-form-row">
                                        <div class="input-field col s12">
                                            <i class="material-icons prefix auth-form-icon">account_circle</i>
                                            <input id="email" type="email" name="email" class="validate" />
                                            <label htmlFor="email">Email</label>
                                        </div>
                                    </div>
                                </Fragment>
                                :
                                <div class="row auth-form-row">
                                    <div class="input-field col s12">
                                        <i class="material-icons prefix auth-form-icon">account_circle</i>
                                        <input id="ename" type="text" name="username" class="validate" />
                                        <label htmlFor="ename">Email or username</label>
                                    </div>
                                </div>
                            }

                            <div class="row auth-form-row">
                                <div class="input-field col s12">
                                    <i class="material-icons prefix auth-form-icon">lock</i>
                                    <input id="password" type="password" name="password" class="validate" />
                                    <label htmlFor="password">Password</label>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col s12 center-align">
                                    <div className="row">
                                        <div className="col s12 submit-btn-container">
                                            <button className="btn modal-close">Submit</button>
                                            {
                                                props.signup ?
                                                <Fragment>
                                                    <a href="/auth/github" className="btn btn-github">Signup with Github</a>
                                                </Fragment>
                                                :
                                                <Fragment>
                                                    <a href="/auth/github" className="btn btn-github">Login with Github</a>
                                                </Fragment>
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <div className="modal-footer">
                <a onClick={props.toggleModal} className="btn">Close</a>
            </div>
        </div>
    )
}

export default Modal;