import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import { Context } from '../provider/index';
import Services from '../services';
import moment from 'moment';

class QuestionLayout extends Component {
    state = {
        _id: '',
        title: '',
        content: '',
        owner: '',
        date: '',
        createdAt: '',
        answers: []
    }

    static contextType = Context;

    componentDidMount() {
        this.service = new Services();
        this.service.getQuestion(this.props.match.params.id)
            .then(res => {
                console.log(res)
                this.setState({
                    _id: res.data.post._id,
                    title: res.data.post.title,
                    content: res.data.post.content,
                    owner: res.data.post.owner.name,
                    createdAt: res.data.post.createdAt,
                    answers: res.data.post.answers
                })
            }).catch(err => {
                console.log(err)
            })
    }

    getPostAnswers = () => {
        this.service = new Services();
        this.service.getAnswers(this.state._id)
            .then( res => {
                this.setState({
                    answers: res.data.answers
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    
    handleSubmitAnswer = (ev) => {
        ev.preventDefault();
        this.services = new Services();
        const form = new FormData(ev.target);
        const content = form.get('answer')

        this.service.createAnswer(this.state._id, content)
        .then( res => {
            this.form.reset();
            this.getPostAnswers()
        })
        .catch( err => {
            console.log(err)
        })
    }

    setForm = (el) => {
        this.form = el;
    }

    render() {
        console.log(this.state.createdAt)
        return (
            <div className="col s7 top-space">
                <div className="row">
                    <div className="row no-margin">
                        <div className="col s12">
                            <h3>{this.state.title}</h3>
                        </div>
                    </div>
                    <div className="row no-margin">
                        <div className="col s12 right-align">
                            <span>{moment(this.state.createdAt).fromNow()}</span>
                        </div>
                    </div>
                </div>
    
                <div className="row">
                    <div className="row">
                        <div className="col s12">
                            <p>{this.state.content}</p>
                        </div>
                    </div>
    
                    <div className="row">
                        <div className="row">
                            <div className="col s12 right-align">
                                <span><b>Asked by: </b>{this.state.owner}</span>
                            </div>
                        </div>
                    </div>
                </div>
    
                <div className="row">
                    <div className="row no-margin answers-title-row">
                        <div className="col s12">
                            <p>{this.state.answers.length} Answers</p>
                        </div>
                    </div>
    
                    <div className="row full-width">
                        {
                            this.state.answers.map( answer => {
                                return (
                                    <div className="col s12 answers-row">
                                        <div className="row no-margin">
                                            <div className="col s12">
                                                <p>{answer.content}</p>
                                            </div>
                                        </div>
    
                                        <div className="row no-margin">
                                            <div className="col s12 right-align">
                                                <span><b>Answered by: </b>{answer.owner.name}</span>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                {
                    this.context.user &&
                    <div className="row">
                        <div className="row">
                            <div className="col s12">
                                <h4>Your Answer</h4>
                            </div>
                        </div>
        
                        <div className="row">
                            <form ref={this.setForm} className="col s12" onSubmit={this.handleSubmitAnswer}>
                                <div className="row">
                                    <div className="col s12">
                                        <textarea name="answer" className="answer-textarea"></textarea>
                                    </div>
                                </div>
        
                                <div className="row">
                                    <div className="col s12">
                                        <button className="btn">Answer</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

export default withRouter(QuestionLayout);