import React, {Fragment} from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import Landing from './components/landing/Landing';
import Main from './components/homepage/Main';
import SearchLayout from './components/homepage/SearchLayout';
import ChatLayout from './components/chat/ChatLayout';
import Footer from './components/footer/Footer';
import {Provider, Context} from './components/provider/index';
import * as serviceWorker from './serviceWorker';
import './index.css';

const section1 = document.getElementById('section1');

ReactDOM.render(
    <Provider>
        <BrowserRouter>
            <Switch>
                <Route exact path="/" component={Landing} />
                <Route exact path="/search" component={SearchLayout}></Route>
                <Route exact path="/public" component={Main}></Route>
                <Route exact path="/question/:id" component={Main}></Route>
                <Context.Consumer>
                    {(context) => {
                        if (context.user) {
                            return (
                                <Fragment>
                                    <Route exact path="/feed" component={Main}></Route>
                                    <Route exact path="/profile" component={Main}></Route>
                                    <Route exact path="/chat/:userEmitter/:userReceiver" component={ChatLayout}></Route>
                                </Fragment>
                            )
                        } else {
                            return (
                                <Redirect to="/"></Redirect>
                            )
                        } 
                    }}
                </Context.Consumer>
            </Switch>
            <Footer />
        </BrowserRouter>
    </Provider>,
    section1
)

serviceWorker.unregister();
