import axios from 'axios';

class AuthService {
  constructor() {
    let service = axios.create({
      baseURL: process.env.REACT_APP_BASEURL,
      withCredentials: true
    });
    this.service = service;
  }

  signup = (name, username, email, password) => {
    const body = { name, username, email, password }
    return this.service.post('/authenticate/signup', body)
  }

  login = (username, password) => {
    const body = {username, password}
    return this.service.post('/authenticate/login', body)
  }

  getCurrentUser = () => {
    return this.service.get('/authenticate/getcurrentuser')
  }

  updateCurrentUser = (name, username, email) => {
    const body = {name, username, email}
    return this.service.put('/authenticate/updateuser/', body)
  }

  getUserLocation = (position) => {
    return this.service.get(`https://api.opencagedata.com/geocode/v1/json?q=${position.coords.latitude}+${position.coords.longitude}&key=614cbe4035a04205bc7d3451acfe5004`)
  }

  getUserById = (id) => {
    return this.service.get(`/authenticate/getuser/${id}`)
  }
  
  getUserActivity = (id) => {
    return this.service.get(`/api/authenticate/getuseractivity/${id}`)
  }

  getPublicPosts = () => {
    return this.service.get('/api/posts/public')
  }

  getFeedPosts = () => {
    return this.service.get('/api/posts/feed')
  }

  createPost = (title, content) => {
    const body = {title, content}
    return this.service.post('/api/post/create', body)
  }

  deletePost = (id) => {
    return this.service.delete(`/api/post/delete/${id}`)
  }

  updatePost = (postInfo) => {
    const body = {...postInfo}
    return this.service.put('/api/post/update', body)
  }

  logoutUser = () => {
    return this.service.post('/authenticate/logout')
  }

  createAnswer = (postID, content) => {
    const body = {post: postID, content}
    return this.service.post('/api/answer/create', body)
  }

  deleteAnswer = (id) => {
    return this.service.delete(`/api/answer/delete/${id}`)
  }

  getAnswers = (postID) => {
    return this.service.get(`/api/answers/${postID}`)
  }

  searchResults = (data) => {
    return this.service.get(`/api/serch/results/${data}`)
  }

  getQuestion = (id) => {
    return this.service.get(`/api/post/${id}`)
  }

  addFriend = (id) => {
    return this.service.post(`/api/friend/add/${id}`)
  }

  removeFriend = (id) => {
    console.log('service', id)
    return this.service.post(`/api/friend/remove/${id}`)
  }

  acceptRequest = (friendID) => {
    return this.service.post(`/api/friend/accept/${friendID}`)
  }

  deniedRequest = (friendID) => {
    return this.service.post(`/api/friend/denied/${friendID}`)
  }
}

export default AuthService;