import React from 'react';
import moment from 'moment';

function Answers(props) {
    return (
        <div className="row answer-row">
            <div className="row no-margin vertical-align">
                <div className="col s11">
                    <p>{props.content}</p>
                </div>
                <div className="col s1"><button className="btn-flat" onClick={() => props.deleteAnswer(props._id)}><i class="material-icons">delete</i></button></div>
            </div>

            <div className="row no-margin">
                <div className="col s4 push-s8 right-align">
                    <p className="post-info">Answered by: {props.owner.name}</p>
                    <p className="post-info post-info-date">{moment(props.createdAt).fromNow()}</p>
                </div>
            </div>
        </div>
    )
}

export default Answers;