import React, {Component} from 'react';
import io from 'socket.io-client';
import Services from '../services';
import { Context } from '../provider';

class UserResults extends Component {
    state = {
        user: this.props.user,
        removeButton: false
    }

    static contextType = Context;

    addFriend = () => {
        this.services = new Services();
        this.services.addFriend(this.state.user._id)
            .then(res => {
                // In here I dont need to update, because you are waiting the response
                this.socket.emit('newFriendRequest', {user: this.context.user, friend: this.state.user})
                this.setState({
                    removeButton: true
                })
            }) .catch(err => {
                console.log(err)
            })
    }

    removeFriend = () => {
        console.log(this.state.user._id)
        this.services = new Services();
        this.services.removeFriend(this.state.user._id)
            .then( res => {
                // This is for updating my friend list
                // this.context.getCurrentUser()
                console.log(res)
                this.socket.emit('removedFriendRequest', {user: this.context.user, friend: this.state.user})
                this.setState({
                    removeButton: false
                })
            })
            .catch( err => {
                console.log(err)
            })
    }

    componentDidMount() {
        this.socket = io(process.env.REACT_APP_CONNECTSERVER)
    }

    render() {
        return (
            <ul className="collection col s6">
                <li className="collection-item avatar">
                    <img src={this.state.user.img} alt="" className="circle" />
                    <span className="title">{this.state.user.name}</span>
                    <p>{this.state.user.username}</p>
                    {
                        this.context.user && this.context.friendsIDs && (
                            this.context.friendsIDs.includes(this.state.user._id) || this.state.removeButton || this.context.user.friends.outcomingRequests.includes(this.state.user._id) ?
                            <button onClick={this.removeFriend} className="btn-flat secondary-content">Remove</button>
                            :
                            <button onClick={this.addFriend} className="btn-flat secondary-content">Add</button>
                        )
                    }
                </li>
            </ul>
        )
    }
}

export default UserResults;