import React, { useContext } from 'react';
import { Context } from '../provider/index';
import { NavLink } from 'react-router-dom';

function SideNav(props) {
    const context = useContext(Context)
    return (
        <div className="col s2 my-sidenav top-space">
            {
                context.user &&
                <div className="row sidenav-row">
                    <i class="material-icons col s2">forum</i>
                    <NavLink to="/feed" activeClassName="is-active" isActive={props.isActiveNav} className="col s10 btn-flat sidenav-link">Feed</NavLink>
                </div>
            }
            <div className="row sidenav-row">
                <i class="material-icons col s2">public</i>
                <NavLink to="/public" activeClassName="is-active" className="col s10 btn-flat sidenav-link">Public</NavLink>
            </div>
        </div>
    )
}

export default SideNav;