import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {withRouter, Link} from 'react-router-dom';
import {Context} from '../provider/index';
import Service from '../services';

import Dropdown from '../widgets/Dropdown';
import Modal from '../widgets/Modal';

import io from 'socket.io-client';

import './header.css';

class Header extends Component {
    state = {
        userDropdown: false,
        notificationDropdown: false,
        messageDropdown: false,
        loginModal: false,
        signupModal: false,
        // notifications: []
    }

    componentDidMount() {
        this.socket = io(process.env.REACT_APP_CONNECTSERVER)
        this.socket.on('friendNotification', (notifications) => {
            // const myNotifications = notifications.filter( notification => {
            //     return notification.userReceiver === this.context.user._id
            // })

            this.context.getCurrentUser()
        })
    }

    static contextType = Context


    handleSubmit = (ev) => {
        ev.preventDefault();
        this.service = new Service();
        const form  = new FormData(ev.target);
        const name = form.get('name');
        const username = form.get('username');
        const email = form.get('email')
        const password = form.get('password');
        const ename = form.get('ename');

        if(ev.target.id === "modal1") {
            this.service.login(username, password)
                .then(res => {
                    this.form1.reset();
                    this.context.authenticateUser(res.data.user)
                    this.props.history.push('/feed')
                    // window.dropdownTrigger()
                })
                .catch( err => {
                    console.log(err)
                })
        } else {
            this.service.signup(name, username, email, password)
                .then( res => {
                    this.form2.reset();
                    this.context.authenticateUser(res.data.user)
                    this.props.history.push('/feed')
                    // window.dropdownTrigger()
                    // window.tabs()
                })
                .catch( err => {
                    console.log(err)
                })
        }
    }

    setForm1 = (el) => {
        this.form1 = el
    }

    setForm2 = (el) => {
        this.form2 = el
    }

    userLogout = () => {
        this.service = new Service();
        this.service.logoutUser()
            .then( (res) => {
                this.socket = io(process.env.REACT_APP_CONNECTSERVER)
                // this.socket.on('connect', () => {
                //     this.socket.emit('web:online', this.context.user)
                // })
                this.socket.emit('logoutEvent', this.context.user)
                this.context.authenticateUser('');
                // window.modalTrigger()
            })
            .catch(err => {
                console.log(err)
            })
    }

    handleSearchForm = (ev) => {
        ev.preventDefault();
        const form = new FormData(ev.target);
        const data = form.get('search');
        // this.context.handleSearchData(data)
        this.props.history.push(`/search?q=${data}`)
        this.context.getCurrentUser()
    }

    toggleDropdown = (dropdown) => {
        this.setState({
            [dropdown]: !(this.state[dropdown])
        })
    }

    toggleModal = (modal) => {
        this.setState({
            [modal]: !(this.state[modal])
        })
    }

    acceptRequest = (friendID) => {
        this.services = new Service();
        this.services.acceptRequest(friendID)
            .then(res => {
                this.socket.emit('acceptFriendRequest', {user: this.context.user, friend: friendID})
                // this.context.getCurrentUser()
            }) .catch(err => {
                console.log(err)
            })
    }

    deniedRequest = (friendID) => {
        this.services = new Service();
        this.services.deniedRequest(friendID)
            .then(res => {
                this.context.getCurrentUser()
            }) .catch(err => {
                console.log(err)
            })
    }

    render() {
        return  ReactDOM.createPortal(
            <nav>
                <div className="my-container full-height">
                    <div className="row no-margin full-height">
                        <div className="col s2 full-height logo-container">
                            <Link to="/" className="title-link">
                                <img src="/assets/logo2.png" width="100px" />
                            </Link>
                        </div>
                        <form className="col s7 no-margin full-height vertical-align" onSubmit={this.handleSearchForm}>
                            <input placeholder="Search" className="search-input" type="text" name="search" />
                        </form>
                        <div className="col s3">
                            {
                                this.context.user ?
                                <ul className="nav-ul">
                                    <li><a onClick={() => this.toggleDropdown('userDropdown')} className='btn my-btn'><i className="material-icons">person</i></a></li>
                                    <li><a onClick={() => this.toggleDropdown('notificationDropdown')} className='btn my-btn'><i className="material-icons">notifications</i></a></li>
                                    <li><a onClick={() => this.toggleDropdown('messageDropdown')} className='btn my-btn'><i className="material-icons">mail</i></a></li>
                                    {
                                        this.state.userDropdown &&
                                        <Dropdown type="userDropdown" userIcon userLogout={this.userLogout}  />
                                    }
                                    {
                                        this.state.notificationDropdown &&
                                        <Dropdown type="notificationDropdown" notificationIcon notifications={this.context.user.friends.incomingRequests} acceptRequest={this.acceptRequest} deniedRequest={this.deniedRequest} />
                                    }
                                    {
                                        this.state.messageDropdown &&
                                        <Dropdown type="messageDropdown" messageIcon />
                                    }
                                </ul>
                                :
                                <ul className="nav-ul">
                                    <li><a onClick={() => this.toggleModal('loginModal')} className="btn modal-trigger">Login</a></li>
                                    <li><a onClick={() => this.toggleModal('signupModal')} className="btn modal-trigger">Signup</a></li>
                                    {
                                        this.state.loginModal &&
                                        <Modal type="modal1" title="Login" login handleSubmit={this.handleSubmit} setForm={this.setForm1} toggleModal={() => this.toggleModal('loginModal')} />
                                    }
                                    {
                                        this.state.signupModal &&
                                        <Modal type="modal2" title="Signup" signup handleSubmit={this.handleSubmit} setForm={this.setForm2} toggleModal={() => this.toggleModal('signupModal')} />
                                    }
                                </ul>
                            }
                        </div>
                    </div>
                </div>
            </nav>,
            document.getElementById('header')
        )
    }
}

export default withRouter(Header);
