import React, { Component, Fragment } from 'react';
import {Link} from 'react-router-dom';
import { Context } from '../provider/index';
import moment from 'moment';

import Answers from './Answers';

import Services from '../services';

class Post extends Component {
    static contextType = Context

    state = {
        post: this.props.post,
        answers: this.props.post.answers,
        newTitle: this.props.post.title,
        newContent: this.props.post.content,
        edit: false,
        viewAnswers: false
    }

    togglePostInfo = () => {
        this.setState({
            edit: !this.state.edit
        })
    }

    sendPostInfo = () => {
        const newInfo = {
            id: this.state.post._id,
            title: this.state.newTitle,
            content: this.state.newContent
        }
        this.props.updatePost(newInfo, () => {
            this.setState({
                edit: false,
                post: {...this.state.post, title: this.state.newTitle, content: this.state.newContent}
            })
        })
    }
    
    handleEditChange = (ev) => {
        this.setState({
            [ev.target.name]: ev.target.value
        })
    }

    sendPostID = () => {
        this.props.deletePost(this.state.post._id)
    }

    toggleAnswers = () => {
        this.setState({
            viewAnswers: !this.state.viewAnswers
        })
    }

    handleAnswerForm = (ev) => {
        ev.preventDefault();
        this.service = new Services();
        const form = new FormData(ev.target);
        const content = form.get('answer')

        this.service.createAnswer(this.state.post._id, content)
            .then( res => {
                this.form.reset();
                this.getPostAnswers()
            })
            .catch( err => {
                console.log(err)
            })
    }

    setAnswerForm = (el) => {
        this.form = el
    }

    getPostAnswers = () => {
        this.service = new Services();
        this.service.getAnswers(this.state.post._id)
            .then( res => {
                console.log(res)
                this.setState({
                    answers: res.data.answers
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    deleteAnswer = (id) => {
        this.sertvice = new Services();
        this.sertvice.deleteAnswer(id)
            .then(res => {
                this.getPostAnswers()
            })
            .catch(err => {
                console.log(err)
            })
    }

    render() {
        return(
            <div className="row post-card">
                <div className="col s12">
                    <div className={`row ${this.state.edit ? '' : 'no-margin'}`}>
                        <div className="col s10">
                            {
                                this.state.edit ?
                                <input name="newTitle" value={this.state.newTitle} onChange={this.handleEditChange} className="edit-question-row"></input>
                                :
                                <Link to={`/question/${this.state.post._id}`}>
                                    <p className="post-title">{this.state.post.title}</p>
                                </Link>
                            }
                        </div>
                        {
                            this.context.user && (
                                this.context.user._id === this.props.post.owner._id && (
                                    <Fragment>
                                        <div className="col s1">
                                            <button className="btn-flat" onClick={this.togglePostInfo}><i class="material-icons">edit</i></button>
                                        </div>

                                        <div className="col s1">
                                            <button className="btn-flat" onClick={this.sendPostID}><i class="material-icons">delete</i></button>
                                        </div>
                                    </Fragment>
                                )
                            )
                        }
                    </div>

                    <div className={`row ${this.state.edit ? '' : 'no-margin'}`}>
                        <div className="col s10 edit-info-container">
                            {
                                this.state.edit ?
                                <textarea className="edit-info-row" name="newContent" value={this.state.newContent} onChange={this.handleEditChange}></textarea>
                                :
                                <p>{this.state.post.content}</p>
                            }
                        </div>
                    </div>

                    <div className="row">
                        <div className="vertical-align">
                            <div className="col s4 no-margin">
                                <button onClick={this.toggleAnswers} className="btn-flat vertical-align"> <i class="material-icons">keyboard_arrow_down</i>{this.state.viewAnswers ? 'Hide Answers' : 'Answers'}</button>
                            </div>
                            <div className="col s5 push-s3 center-align no-margin">
                                <p className="post-info"><b>Asked by: </b>{this.props.post.owner.name}</p>
                                <p className="post-info post-info-date">{moment(this.props.post.createdAt).fromNow()}</p>
                            </div>
                        </div>
                    </div>

                    {
                        this.state.edit &&
                        <div className="row">
                            <div className="col s2 push-s8">
                                <button className="btn-flat" onClick={this.togglePostInfo}>Cancel</button>
                            </div>

                            <div className="col s2 push-s8">
                                <button className="btn" onClick={this.sendPostInfo}>Update</button>
                            </div>
                        </div>
                    }

                    {
                        this.state.viewAnswers &&
                        <Fragment>
                            <div className="container answers-container"> 
                                {
                                    this.state.answers.map( answer => <Answers {...answer} deleteAnswer={this.deleteAnswer} />)
                                }
                            </div>

                            <div className="row no-margin form-answer-container">
                                <form ref={this.setAnswerForm} className="col s12" onSubmit={this.handleAnswerForm}>
                                    <div className="row">
                                        <div className="col s10">
                                            <input className="answer-input" name="answer"></input>
                                        </div>

                                        <div className="col s2">
                                            <button className="btn answer-button">Answer</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </Fragment>
                    }
                </div>
            </div>
        )   
    }
}

export default Post;