import React, {Component} from 'react';
import {Context} from '../provider/index';
import { Link } from 'react-router-dom';

import io from 'socket.io-client';

class UsersPanel extends Component {
    state = {
        onlineUsers: []
    }

    static contextType = Context;

    componentDidMount() {
        this.context.getCurrentUser() //added this dangerous line
        this.socket = io(process.env.REACT_APP_CONNECTSERVER)
        this.socket.on('connect', () => {
            this.socket.emit('web:online', this.context.user)
        })
        this.socket.on('loginEvent', (onlineUsers) => {
            console.log('new useer added', onlineUsers)
            this.setState({
                onlineUsers
            })
        })
        this.socket.on('userDisconnect', (onlineUsers) => {
            this.setState({
                onlineUsers
            })
        })
    }
    
    render() {
        return (
            <div className="col s3 top-space">
                <div className="row no-margin">
                    <div className="col s11 push-s1 center-align users-panel">
                        <div className="row no-margin user-box-title">
                            <div className="col s12">
                                <span>Connected users</span>
                            </div>
                        </div>
    
                        <div className="row">
                            <div className="col s12">
                                <ul class="collection users-collection">
                                    {
                                        this.state.onlineUsers.map( user => {
                                            console.log(user)
                                            if(this.context.friendsIDs) {
                                                if(this.context.friendsIDs.includes(user.id) || this.context.user._id === user.id) {
                                                    return (
                                                        <li class="collection-item avatar">
                                                            <img src={user.img} alt="" class="circle" />
                                                            {
                                                                this.context.user._id === user.id ?
                                                                <span class="title">{user.name} (Me)</span>
                                                                :
                                                                <span class="title">{user.name}</span>
                                                            }
                                                            <p className="online-text">online</p>
                                                            <Link to={`/chat/${this.context.user._id}/${user.id}`} class="secondary-content"><i className="material-icons send-icon">send</i></Link>
                                                        </li>
                                                    )
                                                }
                                            }
                                        })
                                    }
                                    {/* <li class="collection-item avatar">
                                        <i class="material-icons circle">folder</i>
                                        <span class="title">Title</span>
                                        <p>First Line</p>
                                        <Link to={`/chat/${this.context.user._id}/5d426ffdcd6387450240c352`} class="secondary-content"><i class="material-icons">send</i></Link>
                                    </li>
                                    <li class="collection-item avatar">
                                        <i class="material-icons circle green">insert_chart</i>
                                        <span class="title">Title</span>
                                        <p>First Line</p>
                                        <Link to={`/chat/${this.context.user._id}/5d426ffdcd6387450240c352`} class="secondary-content"><i class="material-icons">send</i></Link>
                                    </li>
                                    <li class="collection-item avatar">
                                        <i class="material-icons circle red">play_arrow</i>
                                        <span class="title">Title</span>
                                        <p>First Line</p>
                                        <Link to={`/chat/${this.context.user._id}/5d426ffdcd6387450240c352`} class="secondary-content"><i class="material-icons">send</i></Link>
                                    </li> */}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default UsersPanel;